  <script>
  import { Line } from 'vue-chartjs'
  import { Chart as ChartJS, Title, Tooltip, Legend, LineElement, PointElement, CategoryScale, LinearScale } from 'chart.js';
  import zoomPlugin from 'chartjs-plugin-zoom';
  
  ChartJS.register(Title, Tooltip, Legend, LineElement, PointElement , LinearScale, CategoryScale, zoomPlugin);
  
  export default {
    name: 'LineChart',
    components: { Line },

    props: {
      allData: Array,
   },

   mounted() {
      const plugin = document.createElement("script");
      plugin.setAttribute(
        "src",
        "https://kit.fontawesome.com/f9e3b5ed5c.js"
      );
      plugin.async = true;
      document.head.appendChild(plugin);
  },


   computed: {
    chartData() {
      return{
          labels: this.allData[2],
          datasets: [{
            label: 'pH',
            data: this.allData[0],
            borderWidth: 2,
            backgroundColor: ["rgba(54, 162, 235, 0.5)", "rgb(255, 28, 214, 0.55)"],
            borderColor: ["rgba(54, 162, 235, 1)", "rgba(255, 28, 214, 1)"]
          }],
        }
      },
      tempData() {
      return{
          labels: this.allData[2],
          datasets: [{
            label: 'Temperature',
            data: this.allData[3],
            borderWidth: 2,
            backgroundColor: ["rgba(54, 162, 235, 0.5)", "rgb(255, 28, 214, 0.55)"],
            borderColor: ["rgba(54, 162, 235, 1)", "rgba(255, 28, 214, 1)"]
          }],
        }
      },
      chesData() {
      return{
          labels: this.allData[2],
          datasets: [{
            label: 'TDS (Total Dissolved Solids)',
            data: this.allData[1],
            borderWidth: 2,
            backgroundColor: ["rgba(54, 162, 235, 0.5)", "rgb(255, 28, 214, 0.55)"],
            borderColor: ["rgba(54, 162, 235, 1)", "rgba(255, 28, 214, 1)"]
          }],
        }
      }
   },

    data() {
      return {
        
        lt: [5, 6, 12],
        st: ["2", "3", "5"],

        ph: [],
        tds: [],
        times: [],


        chartOptions: {
          responsive: true,
          scales: {
            x: {
              border: { display: false },
              grid: {
                display: true,
                drawBorder: false,
              },
              ticks: {
                color: 'rgb(0,0,0)'
              }
            },


            y: {
              border: { display: false },
              grid: {
                display: false,
                drawBorder: false,
              },
              ticks: {
                display: true
              },

            }
          },
          plugins: {
              zoom: {
                pan: {
                  enabled: true,
                },
                zoom: {
                    wheel: {
                      enabled: true,
                    },
                    pinch: {
                      enabled: true
                    },
