<script>
import { updateSetting, updateTimes } from '@/firebase.js';
import { setting, } from '@/firebase.js';
import { useCollection } from "vuefire";
import { computed } from "vue";


export default {
  props: ['num'],
  setup() {

    const { data: collectionData } = useCollection(setting);

    // Create a computed property to derive times from collectionData
    const times = computed(() => {
      if (collectionData.value && collectionData.value.length > 1) {
        console.log("Fetched data:", collectionData.value);
        return collectionData.value[1].times || []; // Safely access times
      }
      console.warn("Expected data structure is not present:", collectionData.value);
      return []; // Default to an empty array
    });

    return {
      times
    };
  },
  methods: {
    pushTimer() {
      updateTimes(this.times);
    },
    deleteTime(index) {
      this.times.splice(index, 1);
    },
    addTime() {

      if (this.times.length < 6) {
        console.log("adding");
        console.log(this.times);

        this.times.push("");
      }

    },
    pushLong() {
      console.log(this.longVar);
      if (this.autoLong) {
        this.longVar = 60;
      }

      updateSetting(this.longVar);
    },
    async takeInterval(){
      const lt = await useCollection(setting)._rawValue;

      this.longVar = lt[0]["interval"];
    }

  },
  data() {
    return {
      autoInterval: true,
      autoLong: false,
      longVar: 10,
    }
  },
  async mounted() {
    await this.takeInterval();

    if (this.longVar == 60) { // Replace this condition with your logic
      this.autoLong = true; // Automatically check the checkbox
    }
  },
}

</script>
