<template>
    <div class="container">
        <h1 class="pagetitle" >{{ page.pageTitle }}</h1>
        <Dashboard v-if="activePage == 0"
            :allData="allData"
        ></Dashboard>
        <TimerPage v-if="activePage == 1"></TimerPage>
        <InformationPage v-if="activePage == 2"></InformationPage>
    </div>
</template>

<style>

.pagetitle {

    padding-top: 50 px;

}

</style>

<script>

import Dashboard from './Dashboard.vue';
import TimerPage from './TimerPage.vue';
import InformationPage from './InformationPage.vue';


export default{
    components: {
        Dashboard, TimerPage, InformationPage
    },
    props: [
        'page',
         'activePage',
         'ph',
         'tds',
         'times',
         'temps'
    ],

    computed: {
        allData() {
            // Prepare and return the array you want to pass to Dashboard
            return [
                this.ph,
                this.tds,
                this.times,
                this.temps
            ];
        }
    }



}

</script>
