<template>

    <div class="sidebar">
        <img src="../assets/logo-web.png" class="logo" @click="hdata($event)">
        <div v-for="(page, index) in pages" :key="index">
            
            <a
                :title="`This link goes to ${page.text} page`"
                :class="{active: activePage == index}"
                @click = "changePage(index)"
            > 
            <img :src="page.file" class="cot"> 
            <span>{{ page.text }}</span>
            </a>
        </div>
        <div class="idk"></div>
        <div class="idk2">
            <button class="logout-btn" @click="logout">Logout</button>
        </div>
    </div>

</template>

<style>


.idk2{
    display: flex;
    align-items:center
}

.logout-btn{
    padding: 6px 24px;
    border: 2px solid rgb(160, 12, 12);
    color: rgb(160,12 ,12);
    background: transparent;
    border-radius: 6px;
    cursor: pointer;
    margin-left: 17.5%;
}
.logout-btn:active{
    transform: scale(0.95);
}
.idk {
    height: 50%;
}

.sidebar .logout-btn{
        visibility: hidden;
        opacity : 0;
        transition: opacity 0.5s ease-in-out ;
    }
    .sidebar:hover .logout-btn{
        visibility:visible;
        opacity: 1;
    }

</style>

<script>

export default {
    props : ['pages', 'activePage'],
    methods: {
        changePage(index) {
        this.$emit('change-page', index);
        },
        hdata(event) {
        this.$emit('pdata', event);
        },
        
        
        logout(event) { 
            event.preventDefault();
            window.location.reload();
            this.$emit('logout')
            console.log("logged out");  
        },


    },
    data(){
        return {
           
        };
    },
};

</script>