<template>
    <SideBar
        v-if="loged"
        :pages="pages"
        :activePage="activePage"
        @change-page="changePage($event)"
        @pdata="takeData($event)"
        @logout="logut($event)"
    ></SideBar>
    <PageViewer
        v-if="loged"
        :page="pages[activePage]"
        :active-page="activePage"
        :ph="this.ph"
        :tds="this.tds"
        :times="this.times"
        :temps="this.temps"
    ></PageViewer>
    <LoginPage
        v-if="!loged"
        @log="changeLog($event)"
    ></LoginPage>
</template>

<style>
    @import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        font-family: "Poppins", sans-serif;
    }


    .container{
        margin: auto;
        padding-left: 80px;
        height: 0px;
    }

     .sidebar {
        padding-top: 15px;
        width: 60px;
        background-color: #f1f1f1;
        position: fixed;
        height: 100%;
        overflow: auto;
        transition: width 0.5s ease-in-out, font-size 0.1s ease-in 0.1s;
        font-size: 0px;
        display: absolute;
    }

    .sidebar::-webkit-scrollbar{
        display: none;
