    }
    .sidebar:hover .logout-btn{
        visibility:visible;
        opacity: 1;
    } */

    .cot{
        width: 30px;
        margin-right: 10px;
    }

    .logo{
        max-width: 70px;
        width:100%;
    }

    .sidebar a {
        display: block;
        color: black;
        padding: 16px;
        text-decoration: none;
        display: flex; /* Use flexbox to align items horizontally */
        align-items: center; /* Center items vertically */
