<script>
import SideBar from './SideBar.vue';
import PageViewer from './PageViewer.vue';
import LoginPage from './LoginPage.vue';
import { latest, setting } from  '@/firebase.js';
import { useCollection } from "vuefire";

function compare( a, b ) {
    console.log(a.time);
    if ( a.time < b.time ){
        return -1;
    }
    if ( a.time > b.time ){
        return 1;
    }
    return 0;
}

export default{
    components: {
        SideBar, PageViewer, LoginPage
    },
    created(){
        document.title = "Hydroponic Control System";
        document.logo = "";
    },
 
    methods: {
        changePage(id){
            this.activePage = id;
        },
        changeLog(jr){ 
            this.loged = jr;
            this.takeData();
        },

        logut(){
            console.log("logggg");
            this.loged = false;
        },
        
        async takeData(){

            this.lt = await useCollection(latest)._rawValue;
            const st = await useCollection(setting)._rawValue;
            this.lt.sort( compare );
            console.log(this.lt);
            console.log(st);

            this.ph =[];
            this.tds =[];
            this.times =[];
            this.temps =[];

            for(const ar in this.lt){

                console.log("s");

                this.ph.push(this.lt[ar]['ph']);
                this.tds.push(this.lt[ar]['tds']);
                this.times.push(this.lt[ar]['time']);
                this.temps.push(this.lt[ar]['temp']);
            } 

            console.log(this.ph);
            console.log(this.tds);
            console.log(this.times);
            console.log(this.temps);

        }
    },

    data(){
        return {
            activePage: 0,
            loged: false, //Change it before deploy it
            lt: [],

            ph: [],
            tds: [],
            times: [],
            temps: [],

            pages: [
                {
                    text:'Dashboard',
                    file: require('../assets/dashboard.png'),
                    pageTitle: 'Dashboard',
                },
                {
                    text:'Timer',
                    file: require('../assets/timer.png'),
                    pageTitle: 'Set time',
                },
                {
                    text:'Information',
                    file: require('../assets/information.png'),
                    pageTitle: 'Information',
                },
            ]
        };
    },
    async mounted() {
        await this.takeData();
    },
};


</script>