export const updateTimes = async (times) => {
    try {
        // Reference to the document in the setting collection
        const timerDocRef = doc(db, "setting", "timer"); // Replace "your_document_id" with the actual ID
        // Update the document with the new times array
        await setDoc(timerDocRef, { times: times }, { merge: true }); // Use merge to avoid overwriting other fields
        console.log('Times updated successfully in Firestore');
    } catch (error) {
        console.error('Error updating times in Firestore:', error);
    }
};

export async function updateSetting(newLong) {
    try {
        // Reference to the timer document in the setting collection
        const timerDocRef = doc(db, "setting", "interval");

        // Update the document with the new time
        await setDoc(timerDocRef, { interval: newLong});

        console.log("Document successfully updated with new time:", newLong);
    } catch (error) {
        console.error("Error updating document: ", error);
    }
}